// === Mixins ===
@mixin font-lato {
    font-family: 'Lato', sans-serif; }
@mixin font-montserrat {
    font-family: 'Montserrat', sans-serif; }
// === / Mixins ===

// === Variables ===
$color-white: #ffffff;
// === / Variables ===

// === Common ===
* {
    box-sizing: border-box; }

body {
    @include font-lato; }
.section-outer {}
.section-inner {
    width: 1180px;
    margin: 0 auto; }
// === / Common ===

// === header ===
.section-header {
    color: #ffffff;
    background: url('../img/bg-header.png') no-repeat 50% 50%;
    background-size: cover;
    position: relative;

    .section-inner {
        height: 100vh;
        position: relative;
        z-index: 10;
        display: flex;
        flex-direction: column; }

    &::before {
        content: '';
        position: absolute;
        z-index: 5;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-color: rgba(20, 39, 41, .6); }
    &-logo {
        font-weight: bold;
        font-size: 24px;
        flex-shrink: 1;
        padding-top: 35px; }
    &-content {
        flex-grow: 1;
        display: flex;
        align-items: center; }
    &-wrapper {
        margin: 0 auto;
        &__title,
        &__subtitle {
            text-align: center; }
        &__title {
            font-weight: bold;
            font-size: 56px;
            line-height: 60px;
            margin-bottom: 16px; }
        &__subtitle {
            @include font-montserrat;

            font-size: 18px;
            line-height: 32px;
            width: 80%;
            margin: 0 auto 44px; }
        &__form {
            display: flex;

            &-input {
                flex: 1;
                padding-right: 40px;

                input {
                    @include font-montserrat;

                    border: none;
                    background: rgba(255, 255, 255, .25);
                    color: $color-white;
                    font-size: 16px;
                    margin: 0;
                    padding: 16px 20px;
                    outline: none;
                    border-radius: 4px;
                    width: 100%;

                    &::placeholder {
                        color: $color-white; } } }
            &-button {
                button {
                    @include font-montserrat;

                    font-size: 16px;
                    margin: 0;
                    padding: 16px 20px;
                    border: none;
                    background: linear-gradient(to right, rgb(254, 152, 91), rgb(254, 79, 167));
                    outline: none;
                    border-radius: 4px;
                    color: $color-white; } } } } }

// === / header ===


// === promo ===
.section-promo {
    padding-top: 120px;
    padding-bottom: 120px; }

.section-promo-1 {
    display: flex;

    &__content,
    &__video {
        flex-basis: 50%; }
    &__content {
        padding-right: 90px;

        .title {}
        .subtitle {}
        .text {} }
    &__video {
        img {
            width: 100%;
            height: auto; } } }
.section-promo-2 {}
// === / promo ===
